body {
    background: #333;
}

footer, 
header {
    background: #000;
    color: #fff;
}

header {
    height: 50px;
    line-height: 50px;
    position: fixed;
    z-index: 500;
    top: 0;
    left: 0;
    right: 0;
}

footer {
    padding: 32px 20px;
    position: relative;
}

.container {
    width: 1300px;
    margin: 50px auto 0;

}

.left-catelog {
    width: 208px;
    position: relative;
    vertical-align: baseline;
    padding: 20px 12px 60px 36px;
}

.right-content {
    width: calc(100% - 208px);
    position: relative;
    vertical-align: baseline;
    background-color: #f4f4f4;
    flex-grow: unset;
}

